:root {
  --card-radius:20px;
}
.card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border: 1px groove #ccc;
    border-radius: var(--card-radius);
    width: 300px;
    position: relative;
    background-color: hsl(0,0%, 98%);
    box-shadow: 5px 5px 510x hsl(0,0%, 35%);
    min-height: max-content;    
  }
  
  .card-image {
    height: 60%;
    width: 100%;
    object-fit: cover;
    border-top-left-radius: var(--card-radius);
    border-top-right-radius: var(--card-radius);
    margin: 0;
    padding: 0;    
  }
  .card-upper-title {
    min-height: 4rem;
  }
  
  .card-title {
    font-size: 1.55rem;
    font-weight: bold;
    min-height: 2rem;
    margin: 1rem 1rem 0.05rem 1rem;
  }
  
  .card-subtitle {
    font-size: 0.75rem;
    min-height: 1rem;
    margin: 0 1rem;
    color: #999;
  }
  
  .card-divider {
    margin: 0.8rem 0;
    height: 2px;    
    border-top: 1.5px solid hsl(0,0%, 35%);
    width: 100%;
  }
  
  .card-text {
    font-size: 1rem;
    color: hsl(0,0%, 35%);
    margin: 0.25rem 0 0.5rem 1rem;
  }

  .card-bottom-field{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    min-height: 3rem;
    padding: 0.25rem 1.25rem 1rem 0.5rem;
  }
  
  .card-cost {
    font-size: 1.25rem;
    font-weight: bold;
    margin: 1rem 0.5rem;
  }
  
  .card-currency {
    font-size: 12px;
    color: #999;
  }
  
  .card-input {
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 5px;
    width: 100%;
    margin-top: 10px;
  }
  
  .card-city {
    font-size: 12px;
    color: #999;
    margin-top: 5px;
  }

  .card-car-price > span {
    font-size: 1.25rem;
    padding-top: 0.5rem;
  }
  .card-button{
    padding: 0.5rem;
    font-size: 1.25rem;
    border: 1px solid hsl(120,50%,80%);
    border-radius: 12px;
    background-color: hsl(120,50%,50%);
    color: hsl(0,0%, 0%);
  }

  .card-button:hover{
    background-color: hsl(120,50%,55%);
    color: hsl(0,0%, 98%);
    border: 1px solid hsl(0,0%, 60%);
    cursor: pointer;
    }
  .card-car-price {
    font-size: 1.95rem;
    margin-left: 0.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .card-car-ruppee-icon{
    height: 1.75rem;
    width: 1.75rem;
  }